import { DetailChord, DetailFixed, DetailInit, DetailSection, ScoreData } from "../../../../redux/store/score/scoreData";

namespace ChordProgUtil {

    export type BuildType = 'blank' | 'custom' | 'template';

    export type ProgBlcok = {
        section: DetailSection;
        chordList: DetailChord[];
    }

    export const buildScoreData = () => {
        
        const data: ScoreData = {
            chordList: [
                { type: 'init', detail: { keyIndex: 0, bpm: 100, scale: 'major', beatSignature: '4/4' } as DetailInit },
                { type: 'fixed', detail: { fixed: 'Start' } as DetailFixed },
                { type: 'section', detail: { labelName: 'Section' } as DetailSection },
                {
                    type: 'chord', detail: {
                        beatLen: 4, minute: { head: 0, tail: 0 }, root: null, on: null, symbolKey: 'major',
                        backing: {
                            voicingList: [],
                            pattern: null,
                            hasError: false
                        }
                    } as DetailChord
                },
                { type: 'fixed', detail: { fixed: 'End' } as DetailFixed },
            ],
            notesList: []
        };
        return data
    }
}

export default ChordProgUtil;